'use strict';

export default[
  '$scope',
  '$ionicPopup',
  function(
    $scope,
    $ionicPopup
  ){
    $scope.data = {}

    $scope.showReplaceDriverPopup = function() {
     // Custom popup
      var replaceDriverPopup = $ionicPopup.show({
        title: 'Enter Phone Number',
        templateUrl: '/templates/replace-driver.html',
        scope: $scope,
        buttons: [
          { text: 'Cancel' },
          {
            text: '<b>Send Job</b>',
            // type: 'button-positive',
            onTap: function(e) {
             if (!$scope.data.replaceDriverNo) {
               //don't allow the user to close unless he enters wifi password
               e.preventDefault();
             } else {
               return $scope.data.replaceDriverNo;
             }
            }
          }
        ]
       });
       replaceDriverPopup.then(function(res) {
          if(res) {
             //TODO: send replace job infor to server

             $ionicPopup.alert({
               template: 'Trip is sent to +65'+ $scope.data.replaceDriverNo+
               '<br>Ops is alerted!'
             });
          } else {
             console.log('Not sure!');
          }
       });
    };


    // When button is clicked, the popup will be shown...
    $scope.showCancelTripPopup = function() {
     // Custom popup
      var cancelTripPopup = $ionicPopup.show({
        title: 'Are you sure?',
        templateUrl: '/templates/job-cancel.html',
        scope: $scope,
        buttons: [
          { text: 'Cancel' },
          {
            text: '<b>Confirm</b>',
            // type: 'button-positive',
            onTap: function(e) {
             if (!$scope.data.cancelTrip || !$scope.data.cancelTripConfirm) {
               //don't allow the user to close unless he enters wifi password
               e.preventDefault();
             } else {
               return $scope.data.cancelTripConfirm;
             }
            }
          }
        ]
       });
       cancelTripPopup.then(function(res) {
          if(res) {
             //TODO: send cancel job infor to server

             $ionicPopup.alert({
               template: 'Trip is cancelled.<br>Passengers and Ops are alerted!'
             });
          } else {
             console.log('Not sure!');
          }
       });
    };


    $scope.showNotifyLatePopup = function() {
     // Custom popup
      var tripLatePopup = $ionicPopup.show({
        title: 'Late?',
        templateUrl: '/templates/job-late.html',
        scope: $scope,
        buttons: [
          { text: 'Cancel' },
          {
            text: '<b>Confirm</b>',
            // type: 'button-positive',
            onTap: function(e) {
             if (!$scope.data.tripLate) {
               //don't allow the user to close unless he enters wifi password
               e.preventDefault();
             } else {
               return $scope.data.tripLate;
             }
            }
          }
        ]
       });
       tripLatePopup.then(function(res) {
          if(res) {
             //TODO: send job late infor to server

             $ionicPopup.alert({
               template: 'Passengers Notified that you will be more than 15 mins late.'
             });
          } else {
             console.log('Not sure!');
          }
       });
    };


}];



